.img-gradient {
    position: relative;
    display: inline-block;
}

.img-gradient:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: inline-block;
    background: rgb(0, 0, 0);
    background: linear-gradient(180deg, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0.4009978991596639) 10%, rgba(251, 120, 64, 0.2) 20%, rgba(251, 120, 64, 0.2) 80%, rgba(0, 0, 0, 0.4) 90%, rgba(0, 0, 0, 1) 100%);
    filter: progid: DXImageTransform.Microsoft.gradient(startColorstr="#000000", endColorstr="#000000", GradientType=1);
}

.img-gradient img {
    display: block;
}

.img-gradient-overlay {
    background: rgb(0, 0, 0);
    background: linear-gradient(180deg, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0.2049194677871149) 10%, rgba(0, 0, 0, 0.5466561624649859) 90%, rgba(0, 0, 0, 1) 100%);
    filter: progid: DXImageTransform.Microsoft.gradient(startColorstr="#000000", endColorstr="#000000", GradientType=1);
}

